import React from "react";

const educationContent = [
  {
    year: "2022",
    degree: "TCC - Trabalho de conclusao de curso",
    institute: "UNICAMP",
    details: "Programa de Iniciao Tecnolgica na UNICAMP, com foco em desenvolvimento de sistemas de informao."
  },
  {
    year: "2017 - 2021",
    degree: "Sistemas de Informao",
    institute: "Universidade Estadual de Campinas (UNICAMP)",
    details: "Curso de graduao em sistemas de informao na UNICAMP, uma das principais universidades brasileiras na rea de tecnologia e inovao."
  },
];

const Education = () => {
  return (
    <ul>
      {educationContent.map((val, i) => (
        <li key={i}>
          <div className="icon">
            <i className="fa fa-briefcase"></i>
          </div>
          <span className="time open-sans-font text-uppercase">{val.year}</span>
          <h5 className="poppins-font text-uppercase">
            {val.degree}
            <span className="place open-sans-font">{val.institute}</span>
          </h5>
          <p className="open-sans-font">{val.details}</p>
        </li>
      ))}
    </ul>
  );
};

export default Education;
